// Colors
// ---------------------------------

// Default

$color-default-black: #000000;
$color-default-white: #ffffff;

// Project palette

$color-ghost-white: #f8f9fc;
$color-crimson: #d10a42;
$color-sapphire: #102654;
$color-grey: #787878;
$color-whisper: #e8e8e8;
$color-torch-red: #ed0233;
$color-torch-red-2: #ff121f;
$color-resolution-blue: #1c3374;
$color-egyptian-blue: #103aae;
$color-light-slate-grey: #74819c;
$color-black-russian: #030a1a;
$color-link-water: #c8c9cc;
$color-dark-cerulean: #14367a;
$color-persian-blue: #2e35ac;

// Gradient

$black-to-right: linear-gradient(90deg, rgba(0, 0, 0, 0.75) 0%, rgba(0, 0, 0, 0) 75%);
$blue-to-red: linear-gradient(90deg, #1c3477 0%, #1c3477 0.01%, #1a3578 39.11%, #73184b 75.5%, #b4022a 100%);
$red-to-blue: linear-gradient(97deg, #ed0233 17.6%, #003eb7 95.31%);
$red-to-blue-2: linear-gradient(128deg, #d10a42 0%, #0041ca 45.58%);


// Typography
// ---------------------------------

$fz-default: 16px;
$ff-alumni: "Alumni Sans", "Arial", sans-serif;
$ff-roboto: "Roboto Condensed", "Arial", sans-serif;

// Viewports
// ---------------------------------

$vp-768: 768px;
$vp-1366: 1366px;
